.sideBarContents {
    border-right: none;
    padding: 16px;
    color: white;
    background-color: #2D3748;
    width: 200px;
    position: relative;
    
    display: flex;
    flex-direction: column;
    align-items: center;
}

    .bookInfo {
        width: 100%;
        
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    
        .bookImage {
            height: 210px;
            width: 140px;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            border-radius: 8px;
        }
        
        .bookTitle {
            line-height: 132%;
            align-items: center;
            text-align: center;
            font-weight: 400;
            font-size: 12px;
            margin-bottom: 0;
        }
    
    .historyContainer {
        height: 50%;
        
        display: flex;
        flex-direction: column;  
        align-items: center;
    }
    
        .historyTitle {
            font-size: 12px;
            font-weight: 400;
            line-height: 15.84px;
            height: 10px;
            
            align-self: flex-start;
        }
        
        .historyInputWrap {
            width: 100%;
            
            align-self: flex-start;
        }
        
            .historyInput {
                outline: none;
                margin: 16px 0;
                width: 100%;
                height: 24px;
                border-radius: 8px;
                border: 1px solid #E2E5E6;
                -webkit-box-shadow: 0px 1px 0px 3px rgba(96,108,128,0.05); 
                box-shadow: 0px 1px 0px 3px rgba(96,108,128,0.05);
            }
        
        .historyInput::placeholder {
            padding-left: 4px;
            color: #C7CED9;
            font-size: 10px;
            font-weight: 700;
            line-height: 15px;
        }
        
        .historyWrap {
            width: 110%;   
            /* height: 90%; */
            overflow: scroll;
            overflow-x: hidden;
        }
        
        .historyWrap::-webkit-scrollbar {
            width: 6px;
            background-color: transparent;
        }
        
        .historyWrap::-webkit-scrollbar-thumb {
            width: 6px;
            background-color: rgba(129, 161, 193, 0.38);
            border-radius: 16px;
        }
        
            .historyUnorderedListTag {
                padding-left: 16px;
                margin-bottom: 12px;
                cursor: pointer;
            }
            
            .historyUnorderedListTag:hover {
                /* opacity: 0.8; */
            }
            
            .historyMessage {
                margin-bottom: 0;
                width: 100%;
            }
            
            .historyMessage:hover {
                opacity: 0.8;
            }
            
            .historyDateAndIcon {
                margin-right: 8px;
                
                display: flex;
                align-items: center;
                justify-content: space-between;
            }
            
                .historyDate {
                    width: 100%;
                }
                
                .historyDate:hover {
                    opacity: 0.8;
                }
                
                .historyIcon {
                    margin-right: 8px;
                    
                    display: flex;
                    justify-content: space-between;
                }
                
                    .readOnly {
                        margin-right: 6px;
                        transition: all 0.3s;
                    }
                    
                    .readOnly:hover {
                        opacity: 0.8;
                        transform: scale3d(1.5, 1.5, 1.5);
                    }
                    
                    .rollBack {
                        transition: all 0.3s;
                    }
                
                    .rollBack:hover {
                        opacity: 0.8;
                        transform: scale3d(1.5, 1.5, 1.5);
                    }

.sideBarToggleButton {
    border-radius: 0 16px 16px 0;
    border-left: none;
    background-color: #2D3748;
    color: var(--color-white);
    cursor: pointer;
    width: 16px;
    height: 100%;
    /* -webkit-box-shadow: 7px 3px 9px 3px rgba(0,0,0,0.5);  */
    box-shadow: 12px 3px 6px 1px rgba(0,0,0,0.5);
}